html,
body {
  height: 100%;
  font: normal normal 17px/1.235em "Arial", sans-serif; }

.body-wrapper {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  flex-grow: 1; }

.top-page {
  flex-grow: 1; }

.body__footer {
  padding: 45px 80px 43px 59px; }

.nav-list {
  display: flex;
  margin: 0;
  padding: 0; }
  .nav-list__item {
    list-style: none;
    margin-right: 70px; }
    .nav-list__item:last-child {
      margin-right: 0; }
  .nav-list__link {
    display: block;
    position: relative;
    color: #ffffff;
    text-decoration: none;
    text-transform: uppercase; }
    .nav-list__link::after {
      content: "";
      position: absolute;
      top: 20px;
      left: 0;
      width: 16px;
      height: 2px;
      background: #ffffff;
      transition-duration: 0.2s; }
    .nav-list__link:hover::after {
      width: 100%; }

.header-nav__list {
  padding-right: 43px; }

.main-header {
  display: flex;
  justify-content: space-between;
  margin: 0; }

.main-wrapper {
  padding: 36px 138px 0 138px;
  background: url("../img/main-wrapper-bg.jpg") no-repeat; }

.socials {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0; }
  .socials__item {
    margin-right: 15px; }
  .socials__link {
    display: block;
    text-decoration: none;
    height: 25px; }
    .socials__link:hover, .socials__link:focus {
      opacity: 0.5; }
    .socials__link:active {
      opacity: 0.3; }

.footer-links {
  margin: 0;
  padding: 0;
  list-style: none; }
  .footer-links__link {
    font-size: 14px;
    line-height: 16px;
    text-decoration: none;
    color: #ffffff; }
    .footer-links__link:hover, .footer-links__link:focus {
      outline: 0.3;
      opacity: 0.5; }
    .footer-links__link:active {
      opacity: 0.3; }

.footer-desc__title {
  position: relative;
  margin: 0 0 20px 0;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff; }
  .footer-desc__title::after {
    content: "";
    position: absolute;
    top: 16px;
    left: 0;
    width: 139px;
    height: 2px;
    background: #ffffff; }

.footer-desc__text {
  margin: 0;
  font-size: 12px;
  line-height: 16px;
  color: #ffffff;
  opacity: 0.7; }

.footer-contacts__title {
  position: relative;
  margin: 0 0 20px 0;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff; }
  .footer-contacts__title::after {
    content: "";
    position: absolute;
    top: 16px;
    left: 0;
    width: 139px;
    height: 2px;
    background: #ffffff; }

.footer-contacts__text {
  margin: 0; }

.footer-contacts__link, .footer-contacts__address {
  display: block;
  font-style: normal;
  font-size: 12px;
  line-height: 16px;
  color: #ffffff;
  opacity: 0.7;
  text-decoration: none; }

.footer-contacts__link:hover, .footer-contacts__link:focus {
  outline: none;
  opacity: 1; }

.footer-contacts__link:active {
  opacity: 0.3; }

.main-footer {
  display: flex;
  background: #0f0f0f; }
  .main-footer__desc {
    width: 425px;
    margin-right: 100px; }
  .main-footer__contacts {
    margin-right: 150px; }
  .main-footer__socials {
    margin: auto 0 0 auto; }

.banner {
  min-height: 800px;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  padding-bottom: 30px; }
  .banner__logo {
    align-self: center; }
  .banner__link {
    text-decoration: none;
    position: relative;
    color: #ffffff; }
    .banner__link::after {
      content: "";
      position: absolute;
      top: 0;
      left: -22px;
      width: 19px;
      height: 19px;
      background: url("../img/call.png") no-repeat;
      z-index: 9; }

.index-desc {
  padding: 80px 80px 180px 80px;
  background: #0f0f0f;
  text-align: center;
  color: #ffffff; }
  .index-desc__title {
    margin: 0 0 78px 0;
    font-weight: bold;
    font-size: 36px;
    line-height: 41px;
    text-transform: uppercase; }
  .index-desc__text {
    margin: 0;
    font-size: 16px;
    line-height: 22px;
    letter-spacing: 0.05em; }

.index-slider {
  position: relative; }
  .index-slider__list {
    padding: 0;
    margin: 0;
    display: flex; }
  .index-slider__item {
    display: flex; }
  .index-slider__btn {
    position: absolute;
    top: 100px;
    left: 100px;
    padding: 0;
    border: none; }
    .index-slider__btn--next {
      left: 1800px;
      transform: rotate(180deg); }
    .index-slider__btn::before, .index-slider__btn::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 69px;
      height: 3px;
      background: #ffffff; }
    .index-slider__btn::after {
      top: -10px;
      width: 20px;
      height: 20px;
      background: transparent;
      border-top: 3px solid #ffffff;
      border-left: 3px solid #ffffff;
      transform: rotate(-45deg); }
